






























import Vue from 'vue';
import Personal from '@/components/CeremonyForms/PersonalInformation.vue';
import Longevity from '@/components/CeremonyForms/Longevity.vue';
import AttendInfo from '@/components/CeremonyForms/AttendInfo.vue';
import PrayFor from '@/components/CeremonyForms/PrayFor.vue';
import FormApi from '@/apis/FormApi';
import { Form, FormModel } from '@/models/FormModel';
import EventApi from '@/apis/EventApi';

export default Vue.extend({
  components: {
    Personal,
    Longevity,
    PrayFor,
    AttendInfo,
  },
  data() {
    return {
      checkMode: true,
      isSubmitted: false,
    };
  },
  computed: {
    memberForm(): FormModel {
      return this.$store.state.form;
    },
    signature(): string {
      return this.$store.state.signature;
    },
    ceremonyForm() {
      return this.$store.state.form.ceremonyForm;
    },
  },
  methods: {
    submit() {
      if (this.ceremonyForm.personalInformation.name !== ''
        && this.ceremonyForm.personalInformation.address !== ''
        && this.ceremonyForm.personalInformation.informMethod !== '') {
        if (this.ceremonyForm.personalInformation.telephone !== ''
          || this.ceremonyForm.personalInformation.phone !== '') {
          if (this.ceremonyForm.personalInformation.informMethod === 'letter') {
            if (this.ceremonyForm.personalInformation.postcode !== '') {
              // 成功區(目前只有檢查personalInformation)
              this.submitForm();
            } else {
              this.$notify({
                type: 'error',
                title: '未完成',
                text: '當通知方式為郵寄時，請記得填寫郵遞區號',
              });
            }
          } else {
            // 成功區(目前只有檢查personalInformation)
            this.submitForm();
          }
        } else {
          this.$notify({
            type: 'error',
            title: '未完成',
            text: '至少要填寫電話或是行動電話',
          });
        }
      } else {
        this.$notify({
          type: 'error',
          title: '未完成',
          text: '請填寫完整個人資料',
        });
      }
    },
    async submitForm() {
      this.$store.commit('updateIsSubmitted', true);
      this.$store.commit('updateSignature', this.signature);
      const submitTime = new Date().toISOString();
      this.$store.commit('updateSubmitTime', submitTime);
      await FormApi.save(this.memberForm);
      this.$store.commit('updateMemberForm', new Form()); // 清空
      // 以下資料重整
      this.reset();
      this.$router.push('/homepage/memberlist');
    },
    reset() {
      FormApi.list().then((memberList) => {
        this.$store.commit('updateMemberList', memberList);
      });
      FormApi.listCeremonyAttendees().then((attendeesList) => {
        this.$store.commit('updateAttendeesList', attendeesList);
      });
      EventApi.retrieve().then((eventInfo) => {
        this.$store.commit('updateEventInfo', eventInfo);
      });
    },
  },
});
